.App {
  text-align: center;
}

.App-logo {
  width: 55px;
  height: 60px;
  pointer-events: none;
  object-fit: contain;
  z-index: 10;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.Img-carousel {
  pointer-events: none;
  object-fit: contain;
  z-index: 1;
}

.Data-img {
  width: 500px;
  height: 500px;
  pointer-events: none;
  object-fit: contain;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.slick-prev:before,
.slick-next:before {
  color: #61dafb!important;
}

@import url('https://fonts.googleapis.com/css?family=Parisienne|Quicksand&display=swap');
html, body {
	height: 100%;
	margin: 0;
	background: linear-gradient(to bottom,  rgb(143, 138, 138), rgb(80, 80, 80));
}

